
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Group from 'vux/src/components/group/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
name: 'nearbyStore',
directives: {
TransferDom
},
components: {
XDialog,
Group,
},
data() {
return {
nearbyStores: [{
name: '欧普照明美佳美灯饰',
address: '上海市黄浦区复兴中路551号',
distance: 4.3
}, {
name: '欧普照明',
address: '上海市黄浦区打浦路345号',
distance: 4.3
}, {
name: '欧普照明',
address: '上海市黄浦区新闸路356号',
distance: 4.3
}, {
name: '欧普照明',
address: '山东南路与金陵东路交叉口西50米',
distance: 4.3
}],
showDialog: false,
mobile: '',
userName: '',
search: '', //搜索的关键字
}
},
watch: {
showDialog: function(value) {
if(value == false) { //如果弹框关闭，清空用户名和手机号
this.mobile = '';
this.userName = '';
}
}
},
methods: {
handleAppointment() {
var regName = /^([\u4E00-\uFA29]|[\uE7C7-\uE7F3])*$/; 
var regMobile = /^1[0-9]{10}$/; 
if(!this.userName) {
this.$vux.toast.text('请填写用户名');
} else if(!regName.test(this.userName)) {
this.$vux.toast.text('请填写中文姓名');
} else if(!this.mobile) {
this.$vux.toast.text('请填写手机号');
} else if(!regMobile.test(this.mobile)) {
this.$vux.toast.text('请检查手机号');
} else {
this.$vux.toast.text('预约成功');
}
},
handleSearch() {







console.log(this.search)
}
},
mounted() {
var map = new BMap.Map("map"); 
var point = new BMap.Point(121.4866, 31.24387); 
map.centerAndZoom("上海", 13); 
map.enableScrollWheelZoom(true); 
var cityName = "上海";
var index = 0;
var myGeo = new BMap.Geocoder();
var geolocation = new BMap.Geolocation();
var adds = [];
for(var i = 0; i < this.nearbyStores.length; i++) {
geocodeSearch(this.nearbyStores[i].address, this.nearbyStores[i].name)
}
console.log(adds)

function myFun(result) {
cityName = result.name;
map.setCenter(cityName);
console.log(cityName)
}
var myCity = new BMap.LocalCity();
myCity.get(myFun);

geolocation.getCurrentPosition(function(r) {
if(this.getStatus() == BMAP_STATUS_SUCCESS) {
var mk = new BMap.Marker(r.point);
map.addOverlay(mk);

console.log(r)
} else {
alert('failed' + this.getStatus());
}
}, {
enableHighAccuracy: true
})

function geocodeSearch(add, name) {
myGeo.getPoint(add, function(point) {
console.log(point)
if(point) {
var address = new BMap.Point(point.lng, point.lat);
addMarker(address, new BMap.Label(name, {
offset: new BMap.Size(20, -10)
}));
}
}, cityName);
}

function addMarker(point, label) {
var marker = new BMap.Marker(point);
map.addOverlay(marker);
marker.setLabel(label);
}

function G(id) {
return document.getElementById(id);
}

var ac = new BMap.Autocomplete( //建立一个自动完成的对象
{
"input": "search",
"location": map
});

ac.addEventListener("onhighlight", function(e) { //鼠标放在下拉列表上的事件
var str = "";
var _value = e.fromitem.value;
var value = "";
if(e.fromitem.index > -1) {
value = _value.province + _value.city + _value.district + _value.street + _value.business;
}
str = "FromItem<br />index = " + e.fromitem.index + "<br />value = " + value;

value = "";
if(e.toitem.index > -1) {
_value = e.toitem.value;
value = _value.province + _value.city + _value.district + _value.street + _value.business;
}
str += "<br />ToItem<br />index = " + e.toitem.index + "<br />value = " + value;
G("searchResultPanel").innerHTML = str;
});

var myValue;
ac.addEventListener("onconfirm", function(e) { //鼠标点击下拉列表后的事件
var _value = e.item.value;
myValue = _value.province + _value.city + _value.district + _value.street + _value.business;
G("searchResultPanel").innerHTML = "onconfirm<br />index = " + e.item.index + "<br />myValue = " + myValue;

setPlace();
});

function setPlace() {
map.clearOverlays(); 
function myFun() {
var pp = local.getResults().getPoi(0).point; 
map.centerAndZoom(pp, 18);
map.addOverlay(new BMap.Marker(pp)); 
}
var local = new BMap.LocalSearch(map, { //智能搜索
onSearchComplete: myFun
});
local.search(myValue);
}
}
}



