
import XInput from 'vux/src/components/x-input/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
name: 'user_tel',
data() {
return {
mobile: '', //手机号
code: '', //验证码
getCode: {
txt: '获取验证码',
disabled: false,
}
}
},
methods: {
handleGetCode() {
if(this.mobile) {
this.getCode.disabled = true;
this.getCode.txt = '获取中...';
let seconds = 60;
let self = this;
let time = setInterval(function() {
--seconds;
self.getCode.txt = seconds + 's';
if(seconds <= 0) {
clearInterval(time);
self.getCode.disabled = false;
self.getCode.txt = '获取验证码';
}
}, 1000)
} else {
this.$vux.toast.text('请先填写手机号');
}
},
handleTel() {
if(!this.mobile || !this.code) {
this.$vux.toast.text('您有未填项')
} else {
this.$vux.toast.text('注册成功')
}
this.$router.push({
path:'/my/info',
})
}
},
created() {
window.document.title = '绑定手机';
},
components: {
XInput,
XButton,
Group,
Box
}
}



