export default {
	name: 'cashFlow',
	data() {
		return {
			//underway:提现中，success:提现成功，fail:提现失败
			list:[{
				name:'提现标题',
				state:'underway',
				time:'2017-12-20',
				count:'20.00'
			},{
				name:'提现标题',
				state:'underway',
				time:'2017-12-20',
				count:'20.00'
			},{
				name:'提现标题',
				state:'fail',
				time:'2017-12-20',
				count:'20.00'
			},{
				name:'提现标题',
				state:'success',
				time:'2017-12-20',
				count:'20.00'
			},{
				name:'提现标题',
				state:'success',
				time:'2017-12-20',
				count:'20.00'
			}]
		}
	},
	filters:{
		filtState:function(value){
			if(value == 'underway'){
				value = '提现中';
			}else if(value == 'fail'){
				value = '提现失败';
			}else if(value = 'success'){
				value = '提现成功';
			}
			return value;
		}
	}
}



