selected:'waitAccess'
}
})
},
toService(){

},
toMyInfo() {
this.$router.push({
path:'/My',
query:{
selected:''
}
})
}

},
components: {
Group,
Cell,
CellBox,
Flexbox,
FlexboxItem,
Selector
}
}



