import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import store from '../../components/Store'

export default {
name: 'my_index',
data() {
return {
name: '张三',
mobile: '13265464762'
}
},
created() {
store.set({openid:this.$route.query.openid, time:123456789})
},
methods:{
toWaitPay(){
this.$router.push({
path:'/MyOrders',
query:{
selected:'waitPay'
}
})
},
toWaitGet(){
this.$router.push({
path:'/MyOrders',
query:{
selected:'waitGet'
}
})
},
toWaitAccess(){
this.$router.push({
path:'/MyOrders',
query:{
selected:'waitAccess'
}
})
},
toService(){

},
toMyInfo() {
this.$router.push({
path:'/my/info',
})
}

},
components: {
Group,
Cell,
CellBox,
Flexbox,
FlexboxItem,
}
}



