
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name: 'choiceStore',
data() {
return {
storeList: [{
name: '照明美加美灯饰',
address: '家居建材市场',
distance: '4.3',
checked: true
}, {
name: '照明美加美灯饰',
address: '家居建材市场',
distance: '4.3',
checked: false
},{
name: '照明美加美灯饰',
address: '家居建材市场',
distance: '4.3',
checked: false
}]
}
},
components: {
CheckIcon,
XButton
},
methods:{
handleCheckedItem(item){
this.storeList.forEach((item) => {
item.checked = false;
})
item.checked = true;
},
}
}



