
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import NumKeyboard from '../../components/NumKeyboard.vue'
export default {
name: 'withdrawals',
data() {
return {
showKeyboard:false,
count:'',//提现金额
}
},
methods:{
handleWithdrawals(){
var reg = /^\d+$/;
if(this.count == ''){
this.$vux.toast.show({
text: '请填写提现金额',
type:'text'
})
}else if(!reg.test(this.count)){
this.$vux.toast.show({
text: '金额只能为数字',
type:'text'
})
}else {
this.showKeyboard = true;
this.$vux.loading.show({
text: '请求中...'
})
setTimeout(() => {
this.$vux.loading.hide()
}, 2000)
}
},
handleShow(value){
this.showKeyboard = value;
}
},
components: {
Group,
Cell,
CellBox,
Box,
XButton,
XInput,
NumKeyboard
}
}



