
import Msg from 'vux/src/components/msg/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
data () {
return {

}
},
created() {

if(!window.localStorage.getItem('_pingshifen.user.openid')){
this.$http.get('index.php?s=/Api/Weixin/getOpenid',function (data) {
if (data.success == true) {
window.localStorage.setItem('pingshifen.user.openid', data.data)
} else {
window.location.href = 'http://psf.gailvlunpt.com/index.php/Api/Weixin/start'
}
})
}else{

this.login()
}
},
methods: {
login() {
$this
},
},
components: {
Msg,
Divider,
XButton
},
}



