
import Search from 'vux/src/components/search/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'



export default {
name: 'user_school',
data () {
return {
s_list: [],
s_name: ''
}
},
methods: {
resultClick (item) {
this.s_name = item.title
console.log('点解了时间')
},
getSchool (val) {
let params = {
method:'pingshifen.school.search',
s_name:this.s_name,
}
let _this = this
this.ajax('/api', params, function(data){
_this.s_list = schoolResultToList(data.data)
})

},
submit () {
console.log(this.s_name + '提价了' )
}
},
components: {
Search,
Group,
Cell,
XButton,
CheckIcon
},
}

function schoolResultToList (val) {
let rs = []
for (let key in val) {
rs.push({
title: val[key]['name'],
})
}
return rs
}



