
const APP_NAME='pingshifen';
const STORAGE_KEY = '###pingshifen.local.storage.key.@sn#w#e_';  // localstorage key

export default{
  get(){
    return JSON.parse(window.localStorage.getItem(
      STORAGE_KEY||"[]"))
  },
  set(items){
    window.localStorage.setItem(STORAGE_KEY,JSON.stringify(items))
  }
}




