

import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import App from './App'
import router from './router'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'

import AjaxPlugin from 'vux/src/plugins/ajax/index.js'

import WechatPlugin from 'vux/src/plugins/wechat/index.js'

import global_ from './components/Store'  

Vue.use(ToastPlugin)
Vue.use(LoadingPlugin)
Vue.use(AjaxPlugin)
Vue.use(WechatPlugin)
Vue.use(VueRouter)


FastClick.attach(document.body)

Vue.config.productionTip = false

console.log(AjaxPlugin)

/* eslint-disable no-new */
new Vue({
router,
render: h => h(App)
}).$mount('#app-box')

Vue.prototype.ajax = function (url, params, cb){
var data_str = ''
for (let key in params) {
data_str += (key + '=' + params[key] + '&')
}
if (data_str.length > 0) {
data_str = data_str.substr(0, data_str.length - 1);
}
AjaxPlugin.$http.post(url, data_str)
.then((response) => {
if (cb) cb(response.data)
})
}

Vue.prototype.userInfo = global_



router.beforeEach((to, from, next) => {
if(!window.localStorage.getItem('_pingshifen.user.openid') && to.path != '/weixin_login'){
next('/weixin_login')
return false
}
next()
})



