export default {
	name: 'numKeyboard',
	data() {
		return {
			showNum:[],
			keyNum:[1,2,3,4,5,6,7,8,9]
		}
	},
	methods:{
		handleClick(item){
			if(this.showNum.length<6){
				this.showNum.push(item);
			}
			if(this.showNum.length == 6){
				console.log("提交申请中")
				//处理密码填写完成之后的操作
			}
		},
		deleteInput(){
			if(this.showNum.length>0){
				this.showNum.pop();
			}
		},
		closeKeyboard(){
			this.showNum.splice(0,this.showNum.length);
			this.$emit('showKeyboard',false);
		}
	}
}



